$(document).ready(function() {
    $('input').focus(function () {
        $(this).parent().addClass('focused');
    }).blur(function () {
        $(this).parent().removeClass('focused');
    });

    // [ Î°úÍ∑∏Ïù∏ ]
    $('#loginForm').on('submit', function (event) {
        event.preventDefault(); // Í∏∞Î≥∏ Ìèº Ï†úÏ∂ú ÎèôÏûëÏùÑ ÎßâÏùå

        const requestData = {
            password: $('#password').val(),
            email: $('#email').val(),
        };

        $.ajax({
            url: 'http://localhost:8080/users/login', 
            type: 'POST',
            contentType: 'application/json',
            data: JSON.stringify(requestData),
            xhrFields: {
                withCredentials: true // ÌïÑÏöî Ïãú Ï∂îÍ∞Ä
            },
            crossDomain: true,
            success: function (response, status, xhr) {
                if (xhr.status === 200) {
                    alert(response.body.message);

                    // ÌÜ†ÌÅ∞ Ï†ÄÏû•
                    const accessToken = xhr.getResponseHeader('Authorization');
                    const refreshToken = xhr.getResponseHeader('RefreshToken');

                    localStorage.setItem('Authorization', accessToken);
                    localStorage.setItem('RefreshToken', refreshToken);

                    // home.htmlÎ°ú Ïù¥Îèô
                    window.location.href = 'home';
                }
            },
            error: function (jqXHR) {
                const response = jqXHR.responseJSON;
                if (response != null) {
                    alert(response.body.message);
                } else {
                    alert('Î°úÍ∑∏Ïù∏ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
                }
            }
        });
    });

    //ÌöåÏõêÍ∞ÄÏûÖ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
    $('#navigateButton').click(function () {
        window.location.href = '/signup';
    });
});